
const baseUrl = 'http://localhost:3003/api';

export const fetchSinToken = ( endpoint, data, method = 'GET' ) => {

    console.log( data )

    const url = `${ baseUrl }/${ endpoint }`;

    if ( method === 'GET' ) {

        return fetch( url );

    } else {

        return fetch( url, {

            method,
            headers: {
                'Content-type': 'application/json'
            },
            body: JSON.stringify( data )

        });

    };

};